.App {
  font-family: sans-serif;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
body {
  background-color: #bcbcbc;
  background-image: url("https://transparenttextures.com/patterns/3px-tile.png");
  padding: 0;
  margin: 0;
}
header {
  background-color: orange;
  box-shadow: 1px 3px 10px;
}
h1 {
  padding: 10px;
  display: flex;
  align-items: center;
}
.form {
  border-radius: 10px;
  box-shadow: 1px 3px 10px;
  width: 50%;
  text-align: center;
  background-color: #fff;
  padding: 20px 30px 20px 10px;
  margin: 0 auto;
}
input,
textarea {
  border: none;
  padding: 5px 10px;
  background-color: transparent;
  width: 100%;
  margin-top: 5px;
}
input {
  font-size: 20px;
}
textarea {
  font-size: 16px;
}
.button {
  position: absolute;
  right: 25%;
}
button,
.Fab {
  margin: 0 5px;
  padding: 10px;
  border: none;
  border-radius: 50px;
  background-color: orange;
  font-weight: bold;
  color: #ffffff;
  box-shadow: 1px 3px 10px black;
}
button:hover {
  background-color: yellowgreen;
  color: green;
}
.section {
  margin-top: 50px;
}
.card {
  background-color: #fff;
  width: 20%;
  border-radius: 10px;
  margin: 10px;
  box-shadow: 1px 3px 10px;
  padding: 5px 20px;
}
h2 {
  margin: 10px 0 5px 0;
}
p {
  margin: 0;
}
h4 {
  color: orangered;
  text-align: right;
  margin: 5px;
}
h4:hover {
  color: red;
  cursor: pointer;
}
.section {
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
}
